export default {
	data() {
		return {
			tabList: [{
				name: '优惠券',
				choose: true,
				type: 'coupon'
			}, {
				name: '虚拟物品',
				choose: false,
				type: 'goods'
			}],
			showModal: 'coupon',
			couponList: [],
			goodsList: [],
			pageTotal: 1,
			pageIndex: 1,
			pageSize: 10,
			t_x: 0,
			t_y: 0,
			nomore: false,
			nodata: false
		};
	},
	onShow() {
		this.getCoupon()
	},
	onPullDownRefresh() {
		if (this.showModal == 'coupon') {
			this.getCoupon()
		} else {
			this.setparams()
			this.getReceiveList()
		}
		uni.stopPullDownRefresh()
	},
	onReachBottom() {
		if (this.showModal == 'goods') {
			if (this.pageIndex < this.pageTotal) {
				this.pageIndex++
				this.getReceiveList()
			} else {
				this.nomore = true
			}
		}

	},
	methods: {
		setparams() {
			this.pageIndex = 1
			this.pageSize = 20
			this.nomore = false
			this.nodata = false
			this.goodsList = []
		},
		chooseTap(ind) {
			let tabList = this.tabList
			tabList.filter((v, i) => {
				if (i == ind) {
					v.choose = true
					this.showModal = v.type
				} else {
					v.choose = false
				}
			})
			this.setparams()
			if (this.showModal == 'goods') {
				this.getReceiveList()
			} else {
				this.getCoupon()
			}
			this.tabList = tabList
		},
		getCoupon() {
			let self = this
			let params = {
				pageIndex: 1,
				pageSize: 20,
			}
			self.$acFrame.HttpService.couponsList(params).then(res => {
				if (res.success) {
					let couponList = res.data.rows
					couponList.filter(v => {
						v.hasReceived = false
						v.timeEnd = self.$acFrame.Util.formatTime(v.timeEnd, 'dayhm')
						v.timeStart = self.$acFrame.Util.formatTime(v.timeStart, 'dayhm')
					})
					self.couponList = couponList
				}
			})
		},
		getReceiveList() {
			let self = this
			let params = {
				pageIndex: this.pageIndex,
				pageSize: this.pageSize,
			}
			this.$acFrame.HttpService.myReceiveGoods(params).then(res => {
				if (res.success) {
					let list = res.data.rows
					self.pageTotal = res.data.pageTotal
					if (list.length > 0) {
						list.filter(v => {
							v.style = "left:0;"
						})
						self.goodsList = self.goodsList.concat(list)
					} else {
						this.nodata = true
					}
				}
			})
		},
		backGoods(item) {
			let self =this
			let params = {
				id:item.virtualVo.id
			}
			this.$acFrame.HttpService.myReceiveBack(params).then(res => {
				if (res.success) {
					self.$acFrame.Util.mytotal('回收数量为'+res.data.numTotal +'件，总回收星票'+res.data.priceBackTotal)
					setTimeout(function() {
						this.setparams()
						this.getReceiveList()
					}, 1000);
				}
			})
		},
		touchstart: function(e) {
			this.t_x = e.touches[0].pageX;
			this.t_y = e.touches[0].pageY; // 获取触摸时的原点
			// timeInterval = setInterval(function() {
			//   times++;
			// }, 50);
		},
		touchend: function(e, ind) {
			let touchMoveX = e.changedTouches[0].pageX;
			let touchMoveY = e.changedTouches[0].pageY;
			let goodsList = this.goodsList;
			let tmX = touchMoveX - this.t_x;
			let tmY = touchMoveY - this.t_y;
			let absX = Math.abs(tmX);
			let absY = Math.abs(tmY);
			if (absX > 2 * absY && absX > 5) {
				if (tmX < 0) {
					goodsList.filter((v, i) => {
						if (i == ind) {
							v.style = "left:-280rpx";
						} else {
							v.style = "left:0";
						}
					});
				} else {
					goodsList[ind].style = "left:0";
				}
			}
			this.goodsList = goodsList;
			this.t_y = 0;
			this.t_x = 0;
		},
		chooseConpons(obj, ind) {
			if (obj.statusReceive == 1) {
				uni.navigateTo({
					url: "goodsList?obj=" + JSON.stringify(obj)
				})
			} else if (obj.statusReceive == 4) {

			}

		},
		setImg(src) {
			if (!src) {
				src = ''
			}
			return this.$acFrame.Util.setImgUrl(src);
		}
	}

}
